import { useNavigate } from 'react-router-dom';
import { PATH } from '../../../app/App';

//icons
import errorPageIcon from '../../../assets/icons/error-page.svg';

//styles
import { BackButton, ErrorContent, ErrorImage, ErrorMessage, ErrorSection, ErrorText, ErrorTitle } from "./Error.styled";

interface ErrorPropsType {
	message: string
}

export const Error = (props: ErrorPropsType) => {

	const navigate = useNavigate();

	const goBack = () => {
		navigate(PATH.PROFILE);
	}

	return (
		<ErrorSection>
			<ErrorContent>
				<ErrorImage src={errorPageIcon} />
				<ErrorText>
					<ErrorTitle>!</ErrorTitle>
					<ErrorMessage>  </ErrorMessage>
					<BackButton
						onClick={goBack}
					>
						{''}
					</BackButton>
				</ErrorText>
			</ErrorContent>
		</ErrorSection>
	);
}